/* Chat styling */
body{
  /* margin-top:30px; */
  /* background-image: url('/imgs/bg1.jpg'); */
  background-size: cover;
  background-repeat: no-repeat;
}
@media(min-width:1025px){
  /* body{
      background-image: url('/imgs/bg.jpg');
  } */
}
p{
  margin-bottom: 0;
}
#chat-h{
  width: 100%;
  height: 4rem;
  background-color:#404040eb;
  padding: 1rem;
  -webkit-box-shadow: 0 .5rem 1rem rgba(0,0,0,.15)!important;
          box-shadow: 0 .5rem 1rem rgba(0,0,0,.15)!important;
}

#headerUsername{
  position: relative;
  color:whitesmoke;
  font-size: 1.4rem;
  bottom: 0.5rem;
}

#chat{
  width: 100%;
  padding: 18px;
  position: absolute;
  bottom: 47px;
  top: 3rem;
  overflow-y: scroll;
  margin-bottom: 0;
  z-index: 1;
}
#imgBtn{
  position: absolute;
  right: 5rem;
}
#img{
  max-width: 40vw;
}
#messageForm{
  margin-right: 10px;
  margin-left: 10px;
  bottom: 5px;
}
#msg-submit{
  width: 4rem;
  margin-left: -4rem;
  z-index: 1;
}
#message{
  padding-right: 7rem;
}
.own-msg, .msg{
  margin: 0.1rem;
  padding: 0.3rem;
  border-radius: 5px;
  max-width: 50%;
  overflow-wrap: break-word;
}

.figure {
  margin: 0;
}

.figure-caption{
  color: #f8f9fa;
}

.username {
  font-size: 0.8rem;
}

.msg small, .own-msg small {
  color: #c5c5c5;
  font-weight: 700;
}

#roomHead{
  width: 20%;
  position: relative;
  color:whitesmoke;
  font-size: 1.4rem;
  line-height: 1;
  bottom: 0.5rem;
}
#roomHead small {
  font-size: 60%;
  cursor:pointer;
}
/* END OF CHAT STYLES */

/* START OF GENERAL STYLES */
/* Floating labels */

:root {
  --input-padding-x: .75rem;
  --input-padding-y: .75rem;
}

html,
body {
  height: 100%;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  -ms-flex-pack: center;
  align-items: center;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #f5f5f5;
}

#root{
  width: 100%;
}
.container{
  width: 25em;
}

.form-signin {
  width: 100%;
  max-width: 420px;
  padding: 15px;
  margin: 0 auto;
}

.form-label-group {
  position: relative;
  margin-bottom: 1rem;
}

.form-label-group > input,
.form-label-group > label {
  padding: var(--input-padding-y) var(--input-padding-x);
}

.form-label-group > label {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  margin-bottom: 0; /* Override default `<label>` margin */
  line-height: 1.5;
  color: #495057;
  border: 1px solid transparent;
  border-radius: .25rem;
  -webkit-transition: all .1s ease-in-out;
  -o-transition: all .1s ease-in-out;
  transition: all .1s ease-in-out;
}

.form-label-group input::-webkit-input-placeholder {
  color: transparent;
}

.form-label-group input:-ms-input-placeholder {
  color: transparent;
}

.form-label-group input::-ms-input-placeholder {
  color: transparent;
}

.form-label-group input::placeholder {
  color: transparent;
}

.form-label-group input:not(:placeholder-shown) {
  padding-top: calc(var(--input-padding-y) + var(--input-padding-y) * (2 / 3));
  padding-bottom: calc(var(--input-padding-y) / 3);
}

.form-label-group input:not(:placeholder-shown) ~ label {
  padding-top: calc(var(--input-padding-y) / 3);
  padding-bottom: calc(var(--input-padding-y) / 3);
  font-size: 12px;
  color: #777;
}

/* Responsive */
@media (max-width: 567px){
.container{
  max-width: 20em;
}
}


